
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import {
  BadRatingReason,
  HistoryOrder as HistoryOrderInterface
} from '@/modules/store/data/resources'

const badRatingTranslations: Record<BadRatingReason, string> = {
  WRONG_OR_MISSING_PRODUCT: 'badRating.WRONG_OR_MISSING_PRODUCT',
  PRICING_PROBLEM: 'badRating.PRICING_PROBLEM',
  BAD_PRODUCT_QUALITY: 'badRating.BAD_PRODUCT_QUALITY',
  TASTED_BAD: 'badRating.TASTED_BAD',
  PACKAGING_ISSUES: 'badRating.PACKAGING_ISSUES',
  WRONG_PRODUCTS: 'badRating.WRONG_PRODUCTS',
  MISSING_PRODUCTS: 'badRating.MISSING_PRODUCTS',
  POOR_QUALITY: 'badRating.POOR_QUALITY',
  ALLERGY_NOT_CONSIDERED: 'badRating.ALLERGY_NOT_CONSIDERED',
  CHARGED_WRONG_AMOUNT: 'badRating.CHARGED_WRONG_AMOUNT',
  SPECIAL_STORE_PROMOS: 'badRating.SPECIAL_STORE_PROMOS'
}

@Component({
  name: 'HistoryOrderDetailsRatings'
})
export default class HistoryOrderDetailsRatings extends Vue {
  @Prop() details!: HistoryOrderInterface

  get ratingReasons(): string[] {
    const badRatingReasons = this.details.badRatingReasons || []
    return badRatingReasons.map(reason => badRatingTranslations[reason])
  }
}
