
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import Pair from 'common/Pair.vue'
import HistoryOrder from 'models/HistoryOrder'
import HistoryOrderDetailsCutlery from './HistoryOrderDetailsCutlery.vue'
import { HistoryOrder as HistoryOrderInterface } from '@/modules/store/data/resources'
import { green } from '@/modules/core/styles/export.scss'

@Component({
  name: 'HistoryOrderDetailsMessages',
  components: {
    Pair,
    HistoryOrderDetailsCutlery
  }
})
export default class OrderDetailsMessages extends Vue {
  private color = green
  @Prop() details!: HistoryOrderInterface

  get showCutleryRequest(): boolean {
    return !!this.details.cutleryDetails
  }

  get isCashPaymentMethod(): boolean {
    return !!HistoryOrder.paymentMethodIsCash(this.details)
  }

  get hasAllergyInfo(): boolean {
    return this.details && !!this.details.allergyInfo
  }

  get cashMessage(): string {
    return this.details.marketplace
      ? 'order.messages.customer.payment.CASH'
      : 'order.messages.payment.CASH'
  }
}
