
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import HistoryOrder from 'models/HistoryOrder'
import { HistoryOrder as HistoryOrderInterface } from '@/modules/store/data/resources'
import HistoryOrderDetailsMessages from './HistoryOrderDetailsMessages.vue'
import HistoryOrderDetailsRatings from './HistoryOrderDetailsRatings.vue'
import HistoryOrderDetailsCancelled from './HistoryOrderDetailsCancelled.vue'
import HistoryOrderDetailsRefund from './HistoryOrderDetailsRefund.vue'
import HistoryOrderDetailsIncident from './HistoryOrderDetailsIncident.vue'

@Component({
  name: 'HistoryOrderDetailsDescription',
  components: {
    HistoryOrderDetailsMessages,
    HistoryOrderDetailsRatings,
    HistoryOrderDetailsCancelled,
    HistoryOrderDetailsRefund,
    HistoryOrderDetailsIncident
  }
})
export default class HistoryOrderDetailsDescription extends Vue {
  @Prop() details!: HistoryOrderInterface

  get hasBadRatings(): boolean {
    return !!HistoryOrder.hasBadRatings(this.details)
  }

  get hasMessage(): boolean {
    return (
      this.hasCutleryRequestInfo ||
      this.isCashPaymentMethod ||
      this.hasAllergyInfo ||
      HistoryOrder.hasBadRatings(this.details) ||
      HistoryOrder.hasRefundIncident(this.details)
    )
  }

  get isCashPaymentMethod(): boolean {
    return !!HistoryOrder.paymentMethodIsCash(this.details)
  }

  get isCancelled(): boolean {
    return HistoryOrder.isCancelled(this.details)
  }

  get hasAllergyInfo(): boolean {
    return !!this.details && !!this.details.allergyInfo
  }

  get hasCutleryRequestInfo(): boolean {
    return !!this.details && !!this.details.cutleryDetails
  }

  get hasRefundIncident(): boolean {
    return HistoryOrder.hasRefundIncident(this.details)
  }

  get showIncidents(): boolean {
    return this.isCancelled || this.hasRefundIncident
  }
}
