
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import { HistoryOrder as HistoryOrderInterface } from '@/modules/store/data/resources'

@Component({
  name: 'HistoryOrderDetailsCutlery'
})
export default class HistoryOrderDetailsCutlery extends Vue {
  @Prop() details!: HistoryOrderInterface

  get cutleryRequested(): boolean {
    return (
      !!this.details.cutleryDetails &&
      this.details.cutleryDetails.cutleryRequested
    )
  }

  get cutleryMessage(): string {
    return this.cutleryRequested ? 'cutlery.request.yes' : 'cutlery.request.no'
  }

  get setCutleryIcon(): string {
    return this.cutleryRequested
      ? require('@/assets/images/cutlery-yes.svg')
      : require('@/assets/images/cutlery-no.svg')
  }
}
